<template>
  <div class="user-info">
    <i :class="{'anima': anima}"
       :style="{backgroundImage: `url(${userInfo.avatar})`}"
       @click="handleAnima">
    </i>
    <p class="nickname h2 c-e ta-c">{{userInfo.nickname}}</p>
    <span class="d-b bb anima"></span>
    <p class="intro content c-e">{{userInfo.intro}}</p>
  </div>
</template>

<script>
import avatar from '@/assets/img/user/avatar.jpg'

export default {
  name: 'userInfo',
  data() {
    return {
      userInfo: {
        avatar,
        nickname: 'lm wang',
        intro: '用技术做一些有趣的事'
      },
      anima: true
    };
  },
  components: {},
  computed: {},
  watch: {},
  methods: {
    handleAnima() {
      this.anima = true;
    }
  },
  created() {},
  mounted() {
    window.addEventListener('animationend', () => {
      this.anima = false;
    })
  },
  updated() {},
  destroyed() {}
};
</script>
