    display: block;
    width: 120px;
    height: 120px;
    border-radius: 50%;
    background-size: cover;
    border: 6px solid rgb(233, 233, 233);
    &.anima {
      animation: rotate-anima 0.5s linear 3;
    }
  }
  p {
    padding: 20px 0;
  }
  span {
    &.anima {
      animation: width-anima 1s linear infinite alternate;
    }
  }
}

